import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Icon from './Icon';
import '../scss/Navigation.css';

class Navigation extends Component {
    render() {
        console.log('props NAVIGATION', this.props);
        const {
          title1,
          title2,
          title3,
          title4,
          title5,
          dot1,
          dot2,
          dot3, 
          dot4,
          dot5,
          previousStep,
          followingStep,
          handleClickPreviousStep,
          handleClickFollowingStep
        } = this.props;
      return (
        <div>
            <nav className='navigation-container'>
            <Link to={`/step/${previousStep}`}><button onClick={handleClickPreviousStep}>Anterior</button></Link>
              <ul className='list-steps'>
                <li>
                  <Icon state={dot1}/>
                  <p>{title1}</p>
                </li>
                <li>
                  <Icon state={dot2}/>
                  <p>{title2}</p>
                </li>
                <li>
                  <Icon state={dot3}/>
                  <p>{title3}</p>
                </li>
                <li>
                  <Icon state={dot4}/>
                  <p>{title4}</p>
                </li>
                <li>
                  <Icon state={dot5}/>
                  <p>{title5}</p>
                </li>
              </ul>
              <Link to={`/step/${followingStep}`}><button onClick={handleClickFollowingStep}>Siguiente</button></Link>
            </nav>
            
            
            
            
        </div>
      );
    }
  }

export default Navigation;


